import request from '@/utils/request'

export const getSongsList = ({ id, limit, page }) => {
  return request({
    url: `/api/songs`,
    method: 'get',
    params: {
      playlistId: id,
      limit,
      page
    }
  })
}

export const postSong = (playlistId, data) => {
  return request({
    url: `/api/songs/${playlistId}`,
    method: 'post',
    data
  })
}